.item{
    background-color: #36383a;
    border-radius: 5px;
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.25);
    list-style: none;
    padding: 10px 20px;
    margin-bottom: 1.5rem;
    color: #EEEEEE;
    cursor: pointer;
}

li p {
    display: inline;
    margin-left: 0.5rem;
}